<template>
  <div class="update-list">
    <div class="update-header">
      <div class="header-content">
        <div
          class="header-content-item"
          v-for="item in titleData"
          :key="item.type"
          :class="{ 'active-option': item.type == currentActive }"
          @click="changeCurrentState(item.type)"
        >
          {{ item.text }}
        </div>
      </div>
    </div>
    <div class="update-body">
      <div class="body-container" v-if="!isShowLoading">
        <div
          class="body-container-item"
          v-for="item in renderUpdateData"
          :key="item.season_id"
         
          @click="DeliverDetailData(item)"
        >
        <van-image class="v-ig" :src="item.horizontal_cover"></van-image>
          <div class="container-layer">
            <div class="item-title">{{ item.title }}</div>
            <div class="item-styles">{{ item.bottom_info || "未知" }}</div>
          </div>
        </div>
      </div>
      <div class="lazy-icon" v-if="isShowLoading">
        <van-loading type="spinner" color="#1989fa" />
      </div>
      <div class="update-body-empty" v-if="isShowEmpty">
        {{
          currentActive == "End" ? "完结" : "周" + WeekNumber
        }}没有更新的内容,暂时为空哦~~
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.update-list {
  width: 100%;
  height: 100vh;
  .update-header {
    display: flex;
    width: 100%;
    .header-content {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 40px;
      margin: 0px auto;
      .header-content-item {
        font-size: 14px;
        color: #fff;
        padding: 3px 6px;
        margin: 0px 3px;
      }
      .active-option {
        color: black;
        background: #fff;
        border-radius: 999px;
        box-shadow: 0px 0px 10px gray;
      }
    }
  }
  .update-body {
    position: relative;
  }
  .body-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100vh;
    padding-bottom: 136px;
    overflow: scroll;
    &::-webkit-scrollbar {
      width: 0px;
      height: 0px;
    }
    .body-container-item {
      position: relative;
      width: 32%;
      height: 140px;
      margin-left: 1%;
      margin-top: 1%;
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;
      &::after {
        display: block;
        content: "";
        position: absolute;
        width: 100%;
        height: 36%;
        bottom: 0px;
        background: linear-gradient(0deg, black, rgba(0, 0, 0, 0.08));
        z-index: 1;
      }

      .v-ig{
        width:100%;
        height:100%;
      }

      .container-layer {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        z-index: 2;
      }
      .item-title {
        width: 60%;
        max-height: 50%;
        overflow: hidden;
        text-align: center;
        font-size: 17px;
        color: white;
      }
      .item-styles {
        margin: 10px 0px;
        font-size: 13px;
        padding: 3px 7px;
        border-radius: 999px;
        background: snow;
      }
    }
  }
}
.lazy-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding-bottom: 130px;
}
.update-body-empty {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0px;
  bottom: 0px;
  top: 0px;
  right: 0px;
  padding-bottom: 160px;
  color: #fff;
}
</style>
<script>
import { updateListTitle, weekMate } from "../../utils/index";
import { UpdateListData } from "../../api/index";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      titleData: updateListTitle,
      currentActive: 1,
      renderUpdateData: [],
      isShowLoading: false,
      isShowEmpty: false,
      WeekNumber: "一",
    };
  },
  created() {
    if (this.updateIndex == '') {
      this.currentActive = new Date().getDay();
      // console.log('sdlj...')
    } else {
      this.currentActive = this.updateIndex;
      this.renderUpdateData = [...this.renderCData];
    }
    this.WeekNumber = weekMate[this.currentActive];
    this.initData();

  },
  methods: {
    async changeCurrentState(type) {
      if (this.currentActive == type) {
        return;
      }
      this.currentActive = type;
      this.modifyrenderCData([]);
      this.WeekNumber = weekMate[type];
      this.isShowLoading = true;
      this.isShowEmpty = false;
      if (type != "End") {
        this.renderUpdateData = await UpdateListData({ status: -1, orders: 1 });
        let curStr = this.dealCurrentTime();
        this.dealRenderListData(curStr);
      } else {
        this.renderUpdateData = await UpdateListData({ status: 1, orders: 1 });
      }
      this.isShowLoading = false;
      if (this.renderUpdateData.length == 0) {
        this.isShowEmpty = true;
      }
    },
    async initData() {
      this.isShowLoading = true;
      this.isShowEmpty = false;
      if (this.updateIndex == "") {
        this.renderUpdateData = await UpdateListData({ status: -1, orders: 1 });
        let CurStr = this.dealCurrentTime();
        this.dealRenderListData(CurStr);
      }
      this.isShowLoading = false;
      if (this.renderUpdateData.length == 0) {
        this.isShowEmpty = true;
      }

    },
    dealCurrentTime() {
      let date = new Date();
      let num = date.getDay();
      if (this.currentActive != 0) {
        if (this.currentActive <= num) {
          let n = num - this.currentActive;
          date.setDate(date.getDate() - n);
          let str = this.dealTimeString(date);
          return str;
          // console.log('date==>',str);
        } else {
          let n = this.currentActive - num;
          date.setDate(date.getDate() + n);
          let str = this.dealTimeString(date);
          return str;
          //  console.log('input==>',str);
        }
      } else {
        if (num != 0) {
          let n = 7 - num;
          date.setDate(date.getDate() + n);
          let str = this.dealTimeString(date);
          return str;
          // console.log('out==>',str);
        } else {
          let str = this.dealTimeString(date);
          return str;
          // console.log('7u7==>',str);
        }
      }
    },
    dealTimeString(time) {
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      let day = time.getDate();
      month = month < 10 ? "0" + month : month;
      day = day < 10 ? "0" + day : day;
      return year + "." + month + "." + day;
    },
    dealRenderListData(string) {
      this.renderUpdateData = this.renderUpdateData.filter((it) => {
        return it.release_time == string;
      });
    },
    DeliverDetailData(data) {
      let item = { ...data, Did: +new Date() };
      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                 let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id&&t==b;
                });
                // console.log('zhengju',find);

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.modifyActive(4);
            this.modifyUpdateIndex(this.currentActive);
            this.modifyrenderCData(this.renderUpdateData);
            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    dealTimeData() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;

      return {
        year,
        month,
        day,
        hour,
        minute,
        second,
      };
    },
    ...mapMutations(["modifyActive", "modifyUpdateIndex", "modifyrenderCData"]),
  },
  computed: {
    ...mapState(["telephoneString", "updateIndex", "renderCData"]),
  },
};
</script>
