<template>
  <div class="history">
    <div class="history-container" v-if="!isEmpty">
      <div class="container-item" v-for="item in historyData" :key="item.type">
        <div class="container-item-title">{{item.type}}</div>
        <div class="container-item-list">
          <div class="item-list-content" v-for="it in item.data" :key="it.data.Did" @click="DeliverDetailData(it.data.season_id)">
            <div class="list-content-img"  v-lazy:background-image="it.data.horizontal_cover"></div>
            <div class="list-content-center">
              <div class="content-center-title">
                <div class="center-title-f">{{it.data.title}}</div>
                <div class="center-title-s">
                  {{it.data.bottom_info||'未知'}}
                </div>
              </div>
              <div class="content-center-right"><span>{{it.time}}</span></div>
            </div>
            <div class="list-content-right">
              <div class="content-right-button" @click="delectItemEvent(it.data.Did)">删除</div>
            </div>
          </div>
        </div>
      </div>
      <div class="container-tips">没有更多啦~</div>
    </div>

    <div class="history-empty" v-if="isEmpty">你还木有阅读漫画记录哦x_x</div>
  </div>
</template>
<style lang="scss" scoped>
.history {
  width: 100%;
  .history-container {
    width: 100%;
    .container-item {
      box-sizing: border-box;
      padding: 10px;
      .container-item-title {
        font-size: 14px;
        height:20px;
        line-height: 20px;
        color: #ccc;
        margin-bottom: 10px;
      }
      .container-item-list {
        width: 100%;
        .item-list-content {
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 120px;
          margin-bottom: 10px;
          .list-content-img {
            width: 100px;
            height: 120px;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
          }

          .list-content-center {
            flex: 1;
            margin: 0px 10px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 120px;
            .content-center-title {
              width: 100%;
              .center-title-f {
                width: 160px;
                height: 30px;
                line-height: 30px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 15px;
              }
              .center-title-s {
                width: 160px;
                height: 20px;
                line-height: 20px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 13px;
                color: gray;
              }
            }
            .content-center-right {
              width: 160px;
              height: 20px;
              line-height: 20px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              font-size: 13px;
              color: gray;
            }
          }

          .list-content-right {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 60px;
            .content-right-button {
              margin: 0px auto;
              padding: 5px 10px;
              border: 1px solid gray;
              border-radius: 999px;
              font-size:14px;
            }
          }
        }
      }
    }
    .container-tips {
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 13px;
      color: #ccc;
    }
  }
  .history-empty {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 360px;
  }
}
</style>
<script>
import { mapState,mapMutations } from "vuex";
export default {
  data() {
    return {
      isEmpty: true,
      historyData: [],
      time1: "",
      todayArr: [],
      yesterdayArr: [],
      earlierArr: [],
    };
  },
  created() {
    this.initHistoryData();
  },
  methods: {
    initHistoryData() {
      if (localStorage.historyRecord) {
        let currentStorage = JSON.parse(localStorage.historyRecord);
        if (currentStorage[this.telephoneString]) {
          let arr = currentStorage[this.telephoneString];
          if (arr.length != 0) {
            this.historyData = this.dealData(arr);
            this.isEmpty = false;
          } else {
            this.isEmpty = true;
          }
        } else {
          this.isEmpty = true;
        }
      } else {
        this.isEmpty = true;
      }
    },
    dealData(arr) {
      let date = new Date();
      this.time1 =
        `${date.getFullYear()}` +
        `${date.getMonth() + 1}` +
        `${date.getDate()}`;
      let currentArr = arr.map((item) => {
        let time2 =
          `${item.time.year}` + `${item.time.month}` + `${item.time.day}`;

        if (this.time1 == time2) {
          return {
            ...item,
            type: "today",
          };
        } else {
          if (
            item.time.year == new Date().getFullYear() &&
            item.time.month == new Date().getMonth() + 1 &&
            new Date().getDate() - item.time.day == 1
          ) {
            return {
              ...item,
              type: "yesterday",
            };
          } else {
            return {
              ...item,
              type: "earlier",
            };
          }
        }
      });

      this.todayArr = [];
      this.yesterdayArr = [];
      this.earlierArr = [];

      currentArr.forEach((item) => {
        if (item.type == "today") {
          this.todayArr = [...this.todayArr, {data:item.data,time:`${item.time.hour}:${item.time.minute}:${item.time.second}`}];
        } else {
          if (item.type == "yesterday") {
            this.yesterdayArr = [...this.yesterdayArr, {data:item.data,time:`${item.time.year}-${item.time.month}-${item.time.day}  ${item.time.hour}:${item.time.minute}:${item.time.second}`}];
          } else {
            this.earlierArr = [...this.earlierArr, {data:item.data,time:`${item.time.year}-${item.time.month}-${item.time.day}`}];
          }
        }
      });
      if (
        this.todayArr.length != 0 &&
        this.yesterdayArr.length != 0 &&
        this.earlierArr.length != 0
      ) {
        currentArr = [
          { data: this.todayArr, type: "今天" },
          { data: this.yesterdayArr, type: "昨天" },
          { data: this.earlierArr, type: "更早" },
        ];
      } else {
        if (this.todayArr.length != 0) {
          if (this.yesterdayArr.length != 0) {
            if (this.earlierArr.length != 0) {
              currentArr = [
                { data: this.todayArr, type: "今天" },
                { data: this.yesterdayArr, type: "昨天" },
                { data: this.earlierArr, type: "更早" },
              ];
            } else {
              currentArr = [
                { data: this.todayArr, type: "今天" },
                { data: this.yesterdayArr, type: "昨天" }
              ];
            }
          } else {
            if (this.earlierArr.length != 0) {
              currentArr = [
                { data: this.todayArr, type: "今天" },
                { data: this.earlierArr, type: "更早" },
              ];
            } else {
              currentArr = [{ data: this.todayArr, type: "今天" }];
            }
          }
        } else {
          if (this.yesterdayArr.length != 0) {
            if (this.earlierArr.length != 0) {
              currentArr = [
                { data: this.yesterdayArr, type: "昨天" },
                { data: this.earlierArr, type: "更早" },
              ];
            } else {
              currentArr = [{ data: this.yesterdayArr, type: "昨天" }];
            }
          } else {
            if (this.earlierArr.length != 0) {
              currentArr = [{ data: this.earlierArr, type: "更早" }];
            }
          }
        }
      }
      // console.log(currentArr);
      return currentArr;
    },
    delectItemEvent(id){
      let currSt=JSON.parse(localStorage.historyRecord);
      let array=currSt[this.telephoneString].filter(item=>{
        return item.data.Did!=id;
      })
      let obj={};
      obj[this.telephoneString]=array;
      localStorage.historyRecord=JSON.stringify({...currSt,...obj});
      this.initHistoryData();
    },
    DeliverDetailData(id){
      this.modifyActive(0);
      this.modifyActiveText('history')
      this.$router.push({
              path: "/detail/"+id,
            });
    },
     ...mapMutations(["modifyActive","modifyActiveText"]),
    
  },
  computed: {
    ...mapState(["telephoneString"]),
  },
};
</script>