<template>
  <div class="work-classification">
    <div class="work-header">
      <div class="work-header-row" v-for="item in headerTitle" :key="item">
        <div
          class="header-row-item"
          v-for="it in optionData[item]"
          :key="it.id"
          :class="{ 'select-option': selectType[item] == it.id }"
          @click="changeCurrentOption(item, it.id)"
        >
          {{ it.name }}
        </div>
      </div>
    </div>
    <div class="work-body">
      <div class="body-container" v-if="!isRunLoading">
        <div
          class="container-item"
          v-for="item in renderData"
          :key="item.season_id"
          @click="DeliverDetailData(item)"
        >
          <van-image class="v-ig" :src="item.horizontal_cover"></van-image>
          <div class="container-layer">
            <div class="item-title">{{ item.title }}</div>
            <div class="item-styles">
              {{ item.bottom_info || "未知" }}
            </div>
          </div>
        </div>
      </div>
      <div class="empty-content" v-if="isCanRun">没有找到此类漫画啊~~</div>
      <div class="show-loading" v-if="isRunLoading">
        <van-loading type="spinner" color="#1989fa" />
      </div>
    </div>
  </div>
</template>
