<script>
import { classificationHeaderTitle } from "../../utils";
import { WorkClassificationTitle, WorkListData } from "../../api/index";
import { mapState, mapMutations } from "vuex";
export default {
  props: ["activeState"],
  data() {
    return {
      headerTitle: classificationHeaderTitle,
      selectType: {
        styles: -1,
        areas: -1,
        prices: -1,
        status: -1,
        orders: 0,
      },
      optionData: {},
      renderData: [],
      isRunLoading: false,
      isCanRun: false,
    };
  },
  created() {
    this.getOptionData();
  },
  methods: {
    async getOptionData() {
      this.selectType = { ...this.workOptionType };
      this.isRunLoading = true;
      this.optionData = await WorkClassificationTitle();
      this.renderData = await WorkListData(this.selectType);

      // console.log(this.renderData);

      this.isRunLoading = false;
      if (this.renderData.length == 0) {
        this.isCanRun = true;
      } else {
        this.isCanRun = false;
      }
    },
    async changeCurrentOption(obj, id) {
      if (this.selectType[obj] == id) {
        return;
      }
      this.isRunLoading = true;
      this.isCanRun = false;
      this.selectType[obj] = id;
      this.renderData = await WorkListData(this.selectType);
      this.isRunLoading = false;
      if (this.renderData.length == 0) {
        this.isCanRun = true;
      } else {
        this.isCanRun = false;
      }
      this.modifySelectType(this.selectType);
    },
    DeliverDetailData(data) {
      let item = { ...data, Did: +new Date() };
      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.modifyActive(this.activeState);
            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    dealTimeData() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;

      return {
        year,
        month,
        day,
        hour,
        minute,
        second,
      };
    },
    ...mapMutations(["modifyActive", "modifySelectType"]),
  },
  computed: {
    ...mapState(["telephoneString", "workOptionType"]),
  },
};
</script>
