          ><span class="iconfont icon-tongzhi"></span
        ></router-link>
        <router-link tag="div" to="/search"
          ><span class="iconfont icon-search"></span
        ></router-link>
        <router-link tag="div" to="/user" class="user-message"
           :style="{backgroundImage:`url(${userImage})`}"><span class="iconfont icon-user" v-show="!isShowUser"></span
        ></router-link>
      </div>
    </div>

    <router-view />
  </div>
</template>

