<script>
import {mapMutations,mapState} from 'vuex'
export default {
  created(){
    this.getUserMessage();
  },
  methods:{
     getUserMessage(){
      if(localStorage.user){
        let Storage=JSON.parse(localStorage.user);
        for(let key in Storage){
          if(Storage[key].isRun){
            this.modifyUserImage(Storage[key].image);
            this.modifyTelephone(key);
            this.modifyUserState(true);
          }
        }
      }else{
       this.modifyUserState(false);
      }
    },
    ...mapMutations(['modifyTelephone','modifyUserState','modifyUserImage'])
  },
  computed:{
    ...mapState(['telephoneString','isShowUser','userImage'])
  }
}
</script>
