export const detailGetImageIndex=(id)=>{
    return axios.get(`https://apis.netstart.cn/bcomic/GetImageIndex?epId=${id}`).then(({data})=>{
        let arr=data.data.images;
        arr=arr.map(item=>{
            return 'https://manga.hdslb.com'+item.path+'@1000w.webp';
        })
        return arr;
    })
}

