<template>
  <div class="home">
    <van-tabs
      v-model="active"
      swipeable
      background="black"
      line-width="0px"
      title-active-color="white"
      @change="Onchange"
    >
      <van-tab v-for="item in selectText" :title="item.text" :key="item.type">
        <div class="main-content">
          <MyBookshelfView
            v-if="item.type == 'book'"
            @jumpPageEvent="jumpPageEvent"
          ></MyBookshelfView>
          <RankingListView v-if="item.type == 'rank'"></RankingListView>
          <RecommendView v-if="item.type == 'recommend'"></RecommendView>
          <WorkClassificationView :activeState='active'
            v-if="item.type == 'classification'" 
          ></WorkClassificationView>
          <UpdateListView v-if="item.type == 'update'"></UpdateListView>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<style lang="scss" scoped>
.home {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: black;
}
.main-content {
  width: 100vw;
  height: 100vh;
}
</style>
<script>
import MyBookshelfView from "../../components/MyBookshelf/MyBookshelfView.vue";
import RankingListView from "../../components/RankingList/RankingListView.vue";
import RecommendView from "../../components/Recommend/RecommendView.vue";
import WorkClassificationView from "../../components/WorkClassification/WorkClassificationView.vue";
import UpdateListView from "../../components/UpdateList/UpdateListView.vue";
import {mapState,mapMutations} from 'vuex'

export default {
  data() {
    return {
      selectText: [
        {
          text: "我的书架",
          type: "book",
        },
        {
          text: "排行榜",
          type: "rank",
        },
        {
          text: "推荐",
          type: "recommend",
        },
        {
          text: "作品分类",
          type: "classification",
        },
        {
          text: "更新列表",
          type: "update",
        },
      ],
      active: 2,
    };
  },
  created(){
    this.initActive();
  },
  methods: {
    jumpPageEvent() {
      this.active = 3;
    },
    initActive(){
      this.active=this.currentActive;
    },
    Onchange(){
      this.modifyActive(this.active);
      this.modifyUpdateIndex('');
      this.modifyrenderCData([]);
    },
    ...mapMutations(['modifyActive','modifyUpdateIndex','modifyrenderCData'])
  },
  computed:{
    ...mapState(['currentActive'])
  },
  components: {
    UpdateListView,
    MyBookshelfView,
    RankingListView,
    WorkClassificationView,
    RecommendView,
  },
};
</script>
