import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    telephoneString:'',
    isShowUser:false,
    userImage:'',
    currentActive:2,
    changActiveText:'chase',
    activeBanner:'',
    workOptionType:{
      styles: -1,
      areas: -1,
      prices: -1,
      status: -1,
      orders: 0,
    },
    updateIndex:'',
    renderCData:[]
  },
  getters: {
  },
  mutations: {
    modifyTelephone(state,tel){
      state.telephoneString=tel;
    },
    modifyUserState(state,judge){
      state.isShowUser=judge;
    },
    modifyUserImage(state,img){
      state.userImage=img;
    },
    modifyActive(state,num){
      state.currentActive=num;
    },
    modifyActiveText(state,text){
      state.changActiveText=text;
    },
    modifyBanner(state,index){
      state.activeBanner=index;
    },
    modifySelectType(state,obj){
      state.workOptionType=obj;
    },
    modifyUpdateIndex(state,num){
      state.updateIndex=num;
    },
    modifyrenderCData(state,arr){
      state.renderCData=[...arr]
    }
  },
  actions: {
  },
  modules: {
  }
})
