import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/Home/HomeView.vue'
import WelfareSocietyView from '@/views/WelfareSociety/WelfareSocietyView.vue'
import NoticeView from '@/views/Notice/NoticeView.vue'
import SearchView from '@/views/Search/SearchView.vue'
import UserInformationView from '@/views/UserInformation/UserInformationView.vue'
import LoginView from '@/views/Login/LoginView.vue'
import RegisterView from '@/views/Login/RegisterView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/welfare',
    name: 'welfare',
    component: WelfareSocietyView
  },
  {
    path: '/notice',
    name: 'notice',
    component: NoticeView
  },
  {
    path: '/search',
    name: 'search',
    component: SearchView
  },
  {
    path: '/user',
    name: 'user',
    component: UserInformationView,
    children:[{
      path:'setting',
      name:'setting',
      component:()=>import('@/views/UserInformation/SetUpView.vue')
    }]
  },
  {
    path:'/detail/:id',
    name:'detail',
    component:()=>import('@/views/Detail/DetailView.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path:'/register',
    name:'register',
    component:RegisterView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
