<template>
  <div class="common-login">
    <div class="common-header">密码登录</div>
    <div class="common-body">
      <div class="body-account-number">
        <input
          type="text"
          name="account"
          id="account"
          placeholder="请输入用户账号"
          ref="accountState"
        />
      </div>
      <div class="body-password">
        <input
          type="password"
          name="password"
          id="password"
          placeholder="请输入密码"
          ref="passwordText"
        />
        <div class="password-text">忘记密码?</div>
      </div>
      <div class="body-other">
        <input type="checkbox" name="check" id="check" v-model="checkState" />
        <div class="body-other-text">
          我已阅读并同意<span>"服务协议"</span>和<span>"隐私政策"</span>
        </div>
      </div>
      <div
        class="login-button"
        :class="{ 'select-button': !isTaggleColor }"
        @click="loginEvent"
      >
        登录
      </div>
      <div
        class="register-button"
        :class="{ 'select-button': isTaggleColor }"
        @click="jumpToRegister"
      >
        注册
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.common-login {
  width: 100%;
  .common-header {
    margin-top: 60px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 17px;
    color: gray;
  }
  .common-body {
    padding: 30px;

    .body-account-number,
    .body-password,
    .body-other {
      display: flex;
      width: 100%;
      margin-bottom: 10px;
    }
    .body-account-number {
      font-size: 14px;
      height: 40px;
      border-bottom: 1px solid gray;

      #account {
        background: none;
        border: none;
        outline: none;
        width: 100%;
        height: 40px;
      }
    }
    .body-password {
      align-items: center;
      font-size: 14px;
      height: 40px;
      border-bottom: 1px solid gray;
      #password {
        flex: 1;
        background: none;
        height: 40px;
        border: none;
      }
      .password-text {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 86px;
        height: 40px;
        color: #aaa;
      }
    }
    .body-other {
      align-items: center;
      margin-top: 30px;
      color: gray;
      font-size: 14px;
      .body-other-text {
        margin-left: 10px;
        span {
          color: skyblue;
        }
      }
    }
    .login-button,
    .register-button {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 30px;
      height: 40px;
      font-size: 15px;
      border-radius: 999px;
      border: 1px solid gray;
    }
    .select-button {
      background: skyblue;
      color: black;
    }
  }
}
</style>
<script>
import { mapMutations } from "vuex";
export default {
  data() {
    return {
      isTaggleColor: false,
      checkState: false,
      temperObject: {},
      passwordData: "",
      userImg:""
    };
  },
  methods: {
    jumpToRegister() {
      this.isTaggleColor = !this.isTaggleColor;
      this.$emit("changeCurrentTaggle");
    },
    loginEvent() {
      this.temperObject = {};
      let tel = this.$refs.accountState.value;
      let pass = this.$refs.passwordText.value;
      if (tel == "") {
        this.$toast.fail("账号不能为空");
        return;
      }
      if (pass == "") {
        this.$toast.fail("密码不能为空");
        return;
      }
      if (!this.checkState) {
        this.$toast.fail("请勾选同意");
        return;
      }
      if (localStorage.user) {
        let currentStorage = JSON.parse(localStorage.user);

        if (currentStorage[tel]) {
          for (let key in currentStorage) {
            if (key == tel) {
              let obj = {};
              obj[tel] = { ...currentStorage[key], isRun: true };
              this.object = { ...this.object, ...obj };
              this.passwordData = currentStorage[key].password;
              this.userImg= currentStorage[key].image;
              
              this.modifyTelephone(tel);
            } else {
              let obj = {};
              obj[key] = { ...currentStorage[key], isRun: false };
              this.object = { ...this.object, ...obj };
            }
          }

          if (pass == this.passwordData) {
            localStorage.user = JSON.stringify(this.object);
            this.modifyUserState(true);
            this.modifyUserImage(this.userImg);
            this.$toast.success("登录成功");
            this.$router.push({
              path: "/user",
            });
          } else {
            this.$toast.fail("密码错误");
            return;
          }
        } else {
          this.$toast.fail("此账号不存在");
        }
      }else{
        this.$toast.fail("此账号不存在");
      }
    },
    ...mapMutations(["modifyTelephone","modifyUserState","modifyUserImage"]),
  },
};
</script>