<template>
  <div class="recommend-view">
    <div class="recommend-area" v-if="!isRunLoading">
      <div class="recommend-header-swiper">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="item in renderRecommendData" :key="item.id">
            <!-- <div
              class="auto-img"
              :style="{ backgroundImage: `url(${item.img})` }"
            ></div> -->
            <van-image :src="item.img" class="auto-img"></van-image>
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="recommend-body">
        <div class="body-container" v-if="!isShowSurplus">
          <div
            class="container-item"
            v-for="item in renderFeedData"
            :key="item.item_id"
            @click="DeliverDetailData(item)"
          >
            <van-image class="v-ig" :src="item.image"></van-image>

            <div class="container-layer">
              <div class="item-title">{{ item.title }}</div>
              <div class="item-styles">
                {{ item.comic_info.styles.join("、") || "未知" }}
              </div>
            </div>
          </div>
        </div>
        <div class="surplus-loading" v-if="isShowSurplus">
          <van-loading type="spinner" color="#1989fa" />
        </div>
      </div>
    </div>
    <div class="show-loading" v-if="isRunLoading">
      <van-loading type="spinner" color="#1989fa" />
    </div>
  </div>
</template>
<style lang="scss" scoped>
.recommend-view {
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  &::-webkit-scrollbar {
    width: 0px;
    height: 0px;
  }
  .recommend-area {
    width: 100vw;
    padding-bottom: 100px;
  }
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  text-align: center;
}
.auto-img {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  height: 160px;
}
.body-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  .container-item {
    position: relative;
    width: 32%;
    height: 160px;
    margin-left: 1%;
    margin-top: 1%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;

    &::after {
      display: block;
      content: "";
      position: absolute;
      width: 100%;
      height: 36%;
      bottom: 0px;
      background: linear-gradient(0deg, black, rgba(0, 0, 0, 0.08));
      z-index: 1;
    }

    .v-ig{
      width:100%;
      height:160px;
    }

    .container-layer {
      position: absolute;
      left: 0px;
      right: 0px;
      top: 0px;
      bottom: 0px;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      z-index: 2;
    }
    .item-title {
      width: 60%;
      max-height: 50%;
      overflow: hidden;
      text-align: center;
      font-size: 17px;
      color: white;
    }
    .item-styles {
      margin: 10px 0px;
      font-size: 13px;
      padding: 3px 7px;
      border-radius: 999px;
      background: snow;
    }
  }
}

.surplus-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 160px;
}

.show-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding-bottom: 93px;
}
</style>
<script>
import { RecommendData, RecommendFeed } from "../../api/index";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      renderRecommendData: [],
      renderFeedData: [],
      isRunLoading: false,
      isShowSurplus: false,
    };
  },
  created() {
    this.getRecommendData();
    this.getRecommendFeed();
  },
  methods: {
    async getRecommendData() {
      this.isRunLoading = true;
      this.renderRecommendData = await RecommendData();
      this.isRunLoading = false;
    },
    async getRecommendFeed() {
      this.isShowSurplus = true;
      this.renderFeedData = await RecommendFeed();
      this.isShowSurplus = false;
      // console.log(this.renderFeedData);
    },
    DeliverDetailData(data) {
      let item = {
        ...data,
        season_id: data.item_id,
        horizontal_cover: data.image,
        bottom_info: data.comic_info.main_style_name,
        Did: +new Date(),
      };
      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.modifyActive(2);
            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    dealTimeData() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;

      return {
        year,
        month,
        day,
        hour,
        minute,
        second,
      };
    },
    ...mapMutations(["modifyActive"]),
  },
  computed: {
    ...mapState(["telephoneString"]),
  },
};
</script>