<template>
  <div class="register">
    <div class="register-header">
      <div class="register-header-left" @click="backLoginPage">
        <span class="iconfont icon-arrowleft"></span>
      </div>
      <div class="register-header-center">注册</div>
    </div>
    <div class="register-body">
      <div class="body-logo">
        <label>
          <div class="logo-icon">
            <span class="iconfont icon-user"></span>

            <div class="logo-layer" ref="img">
              <input
                type="file"
                id="file-img"
                @change="uploadData"
                ref="file"
                accept=".png, .jpeg, .jpg"
              />
            </div>
          </div>
        </label>
      </div>
      <div class="body-input">
        <div class="input-user">
          <div class="input-user-text">昵称:</div>
          <div class="input-user-name">
            <input
              type="text"
              name="user-name"
              id="user-name"
              placeholder="用户名"
              ref="userText"
            />
          </div>
        </div>
        <div class="input-password">
          <div class="input-password-text">密码:</div>
          <div class="input-password-box">
            <input
              type="password"
              name="password"
              id="password"
              placeholder="密码"
              ref="passText"
            />
            <div class="reveal-area1" @click="revealPasswordText">
              <span class="iconfont icon-eye" v-show="isShowPassword"></span>
              <span
                class="iconfont icon-no_eye"
                v-show="!isShowPassword"
              ></span>
            </div>
          </div>
        </div>
        <div class="input-confirm">
          <div class="input-confirm-text">确认密码:</div>
          <div class="input-confirm-password">
            <input
              type="password"
              name="confirm-password"
              id="confirm-password"
              placeholder="确认密码"
              ref="confirmText"
            />
            <div class="reveal-area2" @click="confirmPasswordText">
              <span class="iconfont icon-eye" v-show="isShowConfirm"></span>
              <span class="iconfont icon-no_eye" v-show="!isShowConfirm"></span>
            </div>
          </div>
        </div>
      </div>
      <div class="body-finish">
        <div class="finish-button" @click="finishRegister">完成</div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.register {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  color: #fff;
  background: black;
  z-index: 66;

  .register-header {
    display: flex;
    align-items: center;
    height: 50px;
    .register-header-left {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      .icon-arrowleft {
        font-size: 20px;
      }
    }
    .register-header-center {
      flex: 1;
      text-align: center;
      padding-right: 25px;
    }
  }

  .register-body {
    width: 100%;
    .body-logo {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 160px;

      .logo-icon {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        border-radius: 999px;
        box-shadow: 0px 0px 6px gray;

        .icon-user {
          font-size: 26px;
        }

        .logo-layer {
          position: absolute;
          top: 0px;
          left: 0px;
          width: 60px;
          height: 60px;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center center;
          border-radius: 999px;
          overflow: hidden;

          #file-img {
            display: none;
          }
        }
      }
    }
    .body-input {
      width: 100%;
      padding: 0px 30px;
      .input-user,
      .input-password,
      .input-confirm {
        display: flex;
        align-items: center;
        height: 30px;
        margin-bottom: 16px;
      }
      .input-user-text,
      .input-password-text,
      .input-confirm-text {
        width: 70px;
        text-align: right;
      }
      #user-name,
      #password,
      #confirm-password {
        width: 200px;
        height: 26px;
        font-size: 14px;
        margin-left: 10px;
        background: rgba(255, 255, 255, 0.31);
      }

      .input-password-box,
      .input-confirm-password {
        position: relative;

        .reveal-area1,
        .reveal-area2 {
          position: absolute;
          right: 10px;
          top: 50%;
          transform: translateY(-50%);
          .icon-no_eye {
            font-size: 15px;
            color: gray;
          }
        }
      }
    }

    .body-finish {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 90px;

      .finish-button {
        padding: 10px 30px;
        border-radius: 3px;
        border: 1px solid gray;
      }
    }
  }
}
</style>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      isShowPassword: false,
      isShowConfirm: false,
      imgurl: null,
      temperStorage: {},
    };
  },
  methods: {
    revealPasswordText() {
      if (this.isShowPassword) {
        this.isShowPassword = false;
        this.$refs.passText.type = "password";
      } else {
        this.isShowPassword = true;
        this.$refs.passText.type = "text";
      }
    },
    confirmPasswordText() {
      if (this.isShowConfirm) {
        this.isShowConfirm = false;
        this.$refs.confirmText.type = "password";
      } else {
        this.isShowConfirm = true;
        this.$refs.confirmText.type = "text";
      }
    },
    finishRegister() {
      let UserName = this.$refs.userText.value;
      let currentPassword = this.$refs.passText.value;
      let currentConfirm = this.$refs.confirmText.value;
      if (
        currentPassword == currentConfirm &&
        currentPassword != "" &&
        UserName != "" &&
        this.imgurl != null
      ) {
        if (localStorage.user) {
          let currentStorage = JSON.parse(localStorage.user);
          this.temperStorage = currentStorage;

          for (let key in currentStorage) {
            if (currentStorage[key].name == UserName) {
              this.$toast.fail("该用户名已占用");
              return;
            } else {
              let obj = {};
              obj[key] = {
                ...this.temperStorage[key],
                isRun: false,
              };
              this.temperStorage = { ...this.temperStorage, ...obj };
            }
          }

          let obj = {};
          obj[this.telephoneString] = {
            name: UserName,
            password: currentPassword,
            image: this.imgurl,
            isRun: true,
          };
          localStorage.user = JSON.stringify({ ...this.temperStorage, ...obj });
        } else {
          let obj = {};
          obj[this.telephoneString] = {
            name: UserName,
            password: currentPassword,
            image: this.imgurl,
            isRun: true,
          };
          localStorage.user = JSON.stringify(obj);
        }
        this.modifyUserState(true);
        this.modifyUserImage(this.imgurl);
        this.$toast.success("登录成功");
        this.$router.push({
          path: "/user",
        });
      } else {
        if (currentPassword == "") {
          this.$toast.fail("密码不能为空");
        } else {
          if (currentPassword != currentConfirm) {
            this.$toast.fail("密码前后不一致");
          } else {
            if (UserName == "") {
              this.$toast.fail("用户名不能为空");
            } else {
              this.$toast.fail("请上传头像");
            }
          }
        }
      }
    },
    uploadData() {
      let fram = new FileReader();
      let fr = this.$refs.file.files[0];
      fram.readAsDataURL(fr);
      const self = this;
      fram.onload = function () {
        self.$refs.img.style.backgroundImage = `url(${this.result})`;
        self.imgurl = this.result;
      };
    },
    backLoginPage() {
      this.$router.push({
        path: "/login",
      });
    },
    ...mapMutations(["modifyUserState", "modifyUserImage"]),
  },
  computed: {
    ...mapState(["telephoneString"]),
  },
};
</script>
