<template>
  <div class="search-main">
    <div class="search-header">
      <div class="header-box">
        <div class="header-box-input">
          <div class="box-input-icon">
            <span class="iconfont icon-search"></span>
          </div>
          <div class="box-input-content">
            <input
              type="text"
              name="word-text"
              id="word-text"
              placeholder="请输入你想搜的内容"
              ref="WordText"
              @input="InputKeyWord"
              @keydown.enter="keyboardEvent"
            />
          </div>
          <div
            class="box-input-close"
            v-show="isInputText"
            @click="closeIcontEvent"
          >
            <span class="iconfont icon-close"></span>
          </div>
        </div>
        <div class="header-box-cancel" @click="CancelEvent">取消</div>
      </div>
      <div class="header-list" v-if="isRunSearch">
        <div
          class="header-list-item"
          v-for="it in SearchResult"
          :key="it.id"
          @click="SearchOption(it.id)"
        >
          <div class="list-item-icon">
            <span class="iconfont icon-search"></span>
          </div>
          <div class="list-item-text">{{ it.org_title }}</div>
        </div>
      </div>
    </div>
    <div class="search-body" v-if="!isRunSearch">
      <div class="body-history" v-if="showHistory">
        <div class="history-title">
          <div class="history-title-left">搜索历史</div>
          <div class="history-title-clear" @click="deleteHistoryData">
            <span class="iconfont icon-delect"></span>
          </div>
        </div>
        <div class="history-content">
          <div
            class="history-content-item"
            v-for="(it, index) in historyRecord"
            :key="index"
            @click="historyRecordSearch(it)"
          >
            {{ it }}
          </div>
        </div>
      </div>
      <div class="body-hot">
        <div class="hot-title">热门搜索</div>
        <div class="hot-container">
          <div
            class="hot-container-item"
            v-for="(item, index) in hotData"
            :key="item.title"
            @click="JumpHotPage(item)"
          >
            <div class="container-item-number">{{ index + 1 }}</div>
            <div class="container-item-main">
              <div
                class="item-main-img"
                v-lazy:background-image="item.horizontal_cover"
              ></div>
              <div class="item-main-right">
                <div class="main-right-title">{{ item.title }}</div>
                <div class="main-right-type">
                  {{ item.styles.join("/") || "未知" }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="show-loading" v-if="isRunLoading">
          <van-loading type="spinner" color="#1989fa" />
        </div>
      </div>
      <div class="body-message-list" v-if="isShowPage">
        <div class="message-list-container" v-show="!lazyMessage">
          <div
            class="message-list-item"
            v-for="it in SearchItem"
            :key="it.id"
            @click="DeliverDetailData(it)"
          >
            <div
              class="list-item-img"
              v-lazy:background-image="it.horizontal_cover"
            ></div>
            <div class="list-item-context">
              <div class="item-context-title">
                {{ it.org_title }}
              </div>
              <div class="item-context-bottom">
                <div class="context-bottom-f">
                  {{ it.author_name?.join(" / ") || "未知" }}
                </div>
                <div class="context-bottom-s">
                  {{ it.styles?.join("/") || "未知" }}
                </div>
                <div class="context-bottom-t">
                  {{ it.is_finish == 1 ? "完结" : "连载中" }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="data-loading" v-show="lazyMessage">
          <van-loading type="spinner" color="#1989fa" />
        </div>
      </div>
      <div class="message-list-empty" v-if="isShowTips">
        <div class="list-empty-contianer">
          <div class="list-empty-left"></div>
          <div class="list-empty-center">没有找到相关漫画</div>
          <div class="list-empty-right"></div>
        </div>
        <div class="list-empty-recommend">
          <div class="list-empty-title">更多推荐</div>
          <div class="list-empty-box" v-show="!lazyLoading">
            <div
              class="message-list-item"
              v-for="its in recommendData"
              :key="its.item_id"
              @click="recommendEvent(its)"
            >
              <div
                class="list-item-img"
                v-lazy:background-image="its.image"
              ></div>
              <div class="list-item-context">
                <div class="item-context-title">
                  {{ its.title }}
                </div>
                <div class="item-context-bottom">
                  <div class="context-bottom-f">
                    {{ its.comic_info.decision || "新作" }}
                  </div>
                  <div class="context-bottom-s">
                    {{ its.comic_info.main_style_name || "未知" }}
                  </div>
                  <div class="context-bottom-t">
                    {{ its.is_finish == 1 ? "完结" : "连载中" }}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="loading-area" v-show="lazyLoading">
            <van-loading type="spinner" color="#1989fa" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
