  top: 50px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background: black;
  .list-empty-contianer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 36%;
    .list-empty-left {
      width: 46px;
      height: 1px;
      background: linear-gradient(90deg, transparent, rgb(117, 117, 117), gray);
    }
    .list-empty-center {
      margin: 0px 10px;
      font-size: 15px;
    }
    .list-empty-right {
      width: 46px;
      height: 1px;
      background: linear-gradient(90deg, gray, rgb(117, 117, 117), transparent);
    }
  }
  .list-empty-recommend {
    width: 100%;
    .list-empty-title {
      font-size: 15px;
