</style>
<script>
import {
  SearchHotData,
  SearchKeyWordData,
  RecommendFeed,
} from "../../api/index";
import { mapState } from "vuex";
export default {
  data() {
    return {
      isRunSearch: false,
      isInputText: false,
      isRunLoading: false,
      isShowPage: false,
      isShowTips: false,
      SearchResult: [],
      SearchItem: [],
      timer: null,
      hotData: [],
      historyRecord: [],
      recommendData: [],
      showHistory: false,
      lazyLoading: false,
      lazyMessage: false,
    };
  },
  created() {
    this.getHotData();
    if (localStorage.historyText) {
      let storageData = JSON.parse(localStorage.historyText);
      if (storageData.length != 0) {
        this.showHistory = true;
        this.historyRecord = storageData;
      }
    }
  },
  methods: {
    async getHotData() {
      this.isRunLoading = true;
      this.hotData = await SearchHotData();
      this.isRunLoading = false;
    },
    CancelEvent() {
      this.$router.push({
        path: "/",
      });
    },
    InputKeyWord() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.isRunSearch = false;
      this.isInputText = false;
      let self = this;

      if (this.$refs.WordText.value != "") {
        this.isInputText = true;
      } else {
        this.isInputText = false;
      }

      this.timer = setTimeout(async function () {
        let keyWord = self.$refs.WordText.value;
        if (keyWord != "") {
          self.isInputText = true;
          self.SearchResult = await SearchKeyWordData(keyWord);
          self.SearchResult = self.SearchResult.map((item) => {
            return {
              ...item,
              title: item.title
                .replace('<em class="keyword">', "")
                .replace("</em>", ""),
            };
          });

          if (self.SearchResult.length != 0) {
            // console.log("222", self.showHide);
            // if (!self.showHide) {
            //   console.log('3333');
            self.isRunSearch = true;
            // } else {
            //   self.showHide = false;
            //   console.log('123243')

            // }
          } else {
            self.isRunSearch = false;
            // self.isInputText = false;

            this.isShowTips = true;
          }
        } else {
          self.isRunSearch = false;
          self.isShowPage = false;
          self.isShowTips = false;
          self.SearchResult = [];
          self.SearchItem = [];
        }
      }, 1000);
    },
    SearchOption(id) {
      this.SearchItem = [this.SearchResult.find((item) => item.id == id)];
      this.isRunSearch = false;
      this.isShowPage = true;
      this.$refs.WordText.value = this.SearchItem[0].org_title;
      this.isShowTips = false;

      let find = this.historyRecord.find(
        (item) => item == this.SearchItem[0].org_title
      );
      if (!find) {
        if (this.SearchItem[0].org_title != "") {
          this.historyRecord.push(this.SearchItem[0].org_title);
        }
      }
      localStorage.historyText = JSON.stringify(this.historyRecord);

      if (this.historyRecord.length != 0) {
        this.showHistory = true;
      } else {
        this.showHistory = false;
      }
    },
    async keyboardEvent() {
      let curTest = this.$refs.WordText.value;
      if (curTest == "") {
        return;
      }
      clearTimeout(this.timer);
      this.isRunSearch = false;

      this.isShowPage = true;

      this.lazyMessage = true;

      this.SearchItem = await SearchKeyWordData(curTest);
      this.lazyMessage = false;

      let find = this.historyRecord.find((item) => item == curTest);
      if (!find) {
        if (curTest != "") {
          this.historyRecord.push(curTest);
        }
      }
      localStorage.historyText = JSON.stringify(this.historyRecord);

      if (curTest != "") {
        this.isInputText = true;
      } else {
        this.isInputText = false;
        this.isShowTips = false;
      }

      if (this.historyRecord.length != 0) {
        this.showHistory = true;
      } else {
        this.showHistory = false;
      }

      if (this.SearchItem.length != 0) {
        this.isShowTips = false;
      } else {
        this.isShowPage = false;
        this.isShowTips = true;
        this.lazyLoading = true;
        this.recommendData = await RecommendFeed();
        this.lazyLoading = false;
        // console.log(this.recommendData);
      }
    },
    closeIcontEvent() {
      this.$refs.WordText.value = "";
      this.isInputText = false;
      this.isRunSearch = false;
      this.isShowPage = false;
      this.isShowTips = false;
      this.SearchItem = [];
      this.SearchResult = [];
    },
    deleteHistoryData() {
      this.historyRecord = [];
      this.showHistory = false;
      localStorage.historyText = JSON.stringify(this.historyRecord);
    },
    async historyRecordSearch(text) {
      this.isRunSearch = false;
      this.isShowPage = true;
      this.$refs.WordText.value = text;
      this.isShowTips = false;
      this.isInputText = true;
      this.lazyMessage = true;
      this.SearchItem = await SearchKeyWordData(text);
      this.lazyMessage = false;
      if (this.SearchItem.length == 0) {
        this.isShowTips = true;
        this.lazyLoading = true;
        this.recommendData = await RecommendFeed();
        this.lazyLoading = false;
      } else {
        this.isShowTips = false;
      }
    },
    DeliverDetailData(data) {
      let item = {
        ...data,
        season_id: data.id,
        bottom_info: data.styles[0],
        title: data.org_title,
        Did: +new Date(),
      };

      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    dealTimeData() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;

      return {
        year,
        month,
        day,
        hour,
        minute,
        second,
      };
    },
    JumpHotPage(data) {
      let item = {
        ...data,
        bottom_info: data.styles[0],
        Did: +new Date(),
      };


      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    recommendEvent(data) {
      let item = {
        ...data,
        bottom_info: data.comic_info.styles[0],
        season_id: data.item_id,
        horizontal_cover: data.image,
        Did: +new Date(),
      };


      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
  },
  computed: {
    ...mapState(["telephoneString"]),
  },
};
</script>