<template>
  <div class="ranking-list">
    <div class="ranking-header">
      <div
        class="header-item"
        v-for="(item, index) in titleData"
        :key="item.id"
      >
        <span
          :class="{ 'current-active': item.id == active }"
          @click="changeCurrentState(item.id, index)"
          >{{ item.name }}</span
        >
      </div>
    </div>
    <div class="ranking-content" v-if="!isShowPage">
      <div class="content-title">
        <span class="limit-size">{{ descriptionTitle }}</span>
      </div>
      <div class="content-main">
        <div class="content-list-area" v-if="!isShowLoading">
          <div
            class="content-item"
            v-for="items in renderListData"
            :key="items.comic_id"
            @click="DeliverDetailData(items)"
          >
            <div class="item-left">
              <van-image
                :src="items.vertical_cover"
                class="img-item"
              ></van-image>
              <div class="rank-number">{{ items.last_rank }}</div>
            </div>
            <div class="item-right">
              <div class="item-right-title">{{ items.title }}</div>
              <div class="item-right-description">
                <div class="des-author">
                  {{ items.author.join(" ") || "未知" }}
                </div>
                <div class="des-styles">{{ items.styles }}</div>
                <div class="des-updata">
                  {{
                    items.is_finish == 0
                      ? `更新至${items.last_short_title}话`
                      : "完結"
                  }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="loading-icon" v-if="isShowLoading">
          <van-loading type="spinner" color="#1989fa" />
        </div>
      </div>
    </div>
    <div class="loading-data" v-if="isShowPage">
      <van-loading type="spinner" color="#1989fa" />
    </div>
  </div>
</template>
<style lang="scss" scoped>
.ranking-list {
  box-sizing: border-box;
  display: flex;
  width: 100vw;
  height: 100vh;
  padding-bottom: 96px;
}
.ranking-header {
  width: 20%;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: scroll;

  .header-item {
    flex-shrink: 0;
    font-size: 15px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
  }
  .current-active {
    background: rgb(240, 235, 235);
    color: black;
    padding: 4px 6px;
    border-radius: 999px;
    box-shadow: 0px 0px 12px gray;
  }
}
.ranking-content {
  flex: 1;
  flex-shrink: 0;
  overflow: scroll;
  &::-webkit-scrollbar {
    width: 0px;
    height: 0px;
  }
  .content-title {
    display: flex;
    width: 100%;
    height: 30px;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    color: #fff;
    .limit-size {
      display: block;
      width: 93%;
      text-align: center;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  .content-main {
    position: relative;
    width: 100%;
    .content-item {
      box-sizing: border-box;
      display: flex;
      margin: 0px 20px 20px 20px;
      .item-left {
        box-sizing: border-box;
        position: relative;
        width: 100px;
        height: 120px;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;

        .img-item{
          width:100px;
          height:120px;
        }

        .rank-number {
          position: absolute;
          left: 6px;
          top: 6px;
          font-size: 15px;
          font-weight: bolder;
          color: #fff;
          width: 23px;
          height: 23px;
          border-radius: 999px;
          line-height: 23px;
          text-align: center;
          background: rgba(0, 0, 0, 0.3);
        }
      }
      .item-right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 10px;

        .item-right-title {
          width: 136px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          line-height: 20px;
          font-size: 15px;
          color: #fff;
        }
        .item-right-description {
          font-size: 13px;
          .des-author,
          .des-styles,
          .des-updata {
            width: 136px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            line-height: 20px;
            color: rgb(241, 236, 236);
          }
        }
      }
    }
    .loading-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 300px;
      padding-bottom: 30px;
    }
  }
}
</style>
<script>
import { RankingListTitle, RankingListData } from "../../api/index";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      titleData: [],
      active: 7,
      descriptionTitle: "",
      renderListData: [],
      isShowLoading: false,
      isShowPage: false,
    };
  },
  created() {
    this.getTitleData();
  },
  methods: {
    async changeCurrentState(id, index) {
      if (this.active == id) {
        return;
      }
      this.isShowLoading = true;
      this.active = id;
      this.modifyBanner(id);
      this.descriptionTitle = this.titleData[index].description;
      this.renderListData = await RankingListData(id);
      this.isShowLoading = false;
    },
    async getTitleData() {
      this.isShowPage = true;
      this.titleData = await RankingListTitle();
      this.isShowPage = false;

      if (this.activeBanner == "") {
        this.active = this.titleData[0].id;
      } else {
        this.active = this.activeBanner;
      }

      this.descriptionTitle = this.titleData[0].description;
      this.isShowLoading = true;
      this.renderListData = await RankingListData(this.active);
      this.isShowLoading = false;
    },
    DeliverDetailData(data) {
      let item = {
        ...data,
        season_id: data.comic_id,
        horizontal_cover: data.vertical_cover,
        bottom_info: data.styles,
        Did: +new Date(),
      };
      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    dealTimeData() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;

      return {
        year,
        month,
        day,
        hour,
        minute,
        second,
      };
    },
    ...mapMutations(["modifyBanner"]),
  },
  computed: {
    ...mapState(["telephoneString", "activeBanner"]),
  },
};
</script>