<template>
  <div class="telephone-login">
    <div class="telephone-header">手机登录注册</div>
    <div class="telephone-body">
      <form class="form-list">
        <div class="body-telephone-number">
          <div class="telephone-number-left">+86</div>
          <div class="telephone-number-center">
            <input
              type="tel"
              placeholder="请输入常用手机号"
              name="tel-number"
              maxlength="11"
              id="tel-number"
              ref="telephoneText"
              @input="telephoneTextEvent"
            />
          </div>
          <div class="telephone-number-right" @click="getVerificationCode">
            <span v-show="textType == 'obtain'">获取验证码</span>
            <span v-show="textType == 'time'">{{ times }}s</span>
            <span v-show="textType == 'renew'">重新获取</span>
          </div>
        </div>
        <div class="body-verification-code">
          <input
            type="text"
            placeholder="请输入验证码"
            name="verification"
            id="verification"
            ref="verificationText"
          />
        </div>
        <div class="body-other">
          <input type="checkbox" name="check" id="check" v-model="checkState" />
          <div class="body-other-text">
            我已阅读并同意<span>"服务协议"</span>和<span>"隐私政策"</span>未注册绑定的手机号验证成功后将自动注册
          </div>
        </div>
        <div class="body-submit">
          <button type="button" id="submit-button" @click="loginEvent">
            登录 / 注册
          </button>
        </div>
      </form>
      <div class="popup-layer" v-if="isAcceptCode">
        <div class="layer-container">
          <div class="layer-header">
            <div class="header-code">{{ string }}</div>
            <div class="header-copy" @click="copyCodeEvent">复制</div>
          </div>
          <div class="layer-body">
            您的动态验证码为<a href="javascript:;">{{ string }}</a
            >,请在1分钟内完成验证,谢谢!注:请勿把验证码告知除您以外的任何人!
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.telephone-login {
  width: 100%;
  .telephone-header {
    margin-top: 60px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 17px;
    color: gray;
  }
  .telephone-body {
    display: flex;
    width: 100%;
    justify-content: center;

    .form-list {
      padding: 30px;

      .body-telephone-number,
      .body-verification-code,
      .body-other {
        display: flex;
        height: 40px;
        margin-bottom: 10px;
      }
      .body-telephone-number {
        align-items: center;
        border-bottom: 1px solid gray;
        .telephone-number-left {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 40px;
          height: 40px;
          color: gray;
          font-size: 15px;
        }
        .telephone-number-center {
          font-size: 14px;

          #tel-number {
            display: block;
            background: none;
            border: none;
            outline: none;
            height: 40px;
          }
        }
        .telephone-number-right {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 90px;
          height: 40px;
          font-size: 15px;
          color: skyblue;
        }
      }
      .body-verification-code {
        border-bottom: 1px solid gray;
        #verification {
          width: 100%;
          height: 40px;
          border: none;
          background: none;
          outline: none;
          font-size: 14px;
        }
      }
    }

    .popup-layer {
      display: flex;
      justify-content: center;
      align-items: center;
      position: fixed;
      top: 0px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      background: rgba(0, 0, 0, 0.281);

      .layer-container {
        width: 230px;
        background: #eee;
        padding: 10px;
        border-radius: 5px;

        .layer-header {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: space-between;
          color: black;
          &::after {
            position: absolute;
            content: "";
            left: 0px;
            bottom: 0px;
            width: 100%;
            height: 1px;
            background: linear-gradient(90deg, transparent, black, transparent);
          }

          .header-code {
            font-weight: bold;
          }
          .header-copy {
            background: #fff;
            border-radius: 999px;
            font-size: 15px;
            padding: 10px 30px;
            margin-bottom: 10px;
          }
        }

        .layer-body {
          margin-top: 10px;
          color: gray;
          font-size: 14px;
          a {
            text-decoration: underline;
          }
        }
      }
    }
  }
  .body-other {
    margin-top: 30px;
    font-size: 14px;
    .body-other-text {
      margin-left: 10px;
      line-height: 20px;
      color: gray;
      span {
        color: skyblue;
      }
    }
  }

  .body-submit {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    #submit-button {
      background: none;
      border: none;
      font-size: 15px;
      padding: 10px 30px;
      border: 1px solid #fff;
      border-radius: 999px;
      margin-top: 30px;
    }
  }
}
</style>
<script>
import { code } from "../../utils/index";
import { mapMutations } from "vuex";
export default {
  data() {
    return {
      telephoneNumber: "",
      timer: null,
      timers: null,
      times: 60,
      textType: "obtain",
      judgeState: "empty",
      checkState: false,
      string: "",
      isAcceptCode: false,
      temperStorage: {},
    };
  },
  methods: {
    telephoneTextEvent() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        let currentTelephone = this.$refs.telephoneText.value;
        if (currentTelephone != "") {
          let reg =
            /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
          let res = reg.test(currentTelephone);
          if (res) {
            this.judgeState = "good";
            this.telephoneNumber = currentTelephone;
          } else {
            this.judgeState = "error";
          }
        } else {
          this.judgeState = "empty";
        }
      }, 600);
    },
    getVerificationCode() {
      if (this.judgeState == "good") {
        if (this.textType == "obtain" || this.textType == "renew") {
          this.textType = "time";
          this.getRandomCode();
          this.isAcceptCode = true;
          // console.log(this.string);

          this.timers = setInterval(() => {
            if (this.times < 1) {
              this.times = 60;
              this.textType = "renew";
              this.isAcceptCode = false;
              this.string = "";
              clearInterval(this.timers);
            } else {
              this.times = this.times - 1;
            }
          }, 1000);
        } else {
          return;
        }
      } else {
        if (this.judgeState == "error") {
          this.$toast.fail("手机号有误");
        } else {
          this.$toast.fail("手机号不能为空");
        }
      }
    },
    loginEvent() {
      let currentVerification = this.$refs.verificationText.value;
      if (this.judgeState != "empty") {
        if (currentVerification != "") {
          if (!this.checkState) {
            this.$toast.fail("请勾选同意");
            return;
          } else {
            if (currentVerification == this.string) {
              if (localStorage.user) {
                let currentStorage = JSON.parse(localStorage.user);
                if (currentStorage[this.telephoneNumber]) {

                  this.temperStorage = currentStorage;
                  for (let key in currentStorage) {
                    if (key == this.telephoneNumber) {
                      let obj = {};
                      obj[key] = {
                        ...this.temperStorage[key],
                        isRun: true,
                      };
                      this.temperStorage = { ...this.temperStorage, ...obj };
                    } else {
                      let obj = {};
                      obj[key] = {
                        ...this.temperStorage[key],
                        isRun: false,
                      };
                      this.temperStorage = { ...this.temperStorage, ...obj };
                    }
                  }

                  
                  localStorage.user = JSON.stringify(this.temperStorage);

                  this.modifyTelephone(this.telephoneNumber);
                  this.modifyUserState(true);
                  this.modifyUserImage(
                    currentStorage[this.telephoneNumber].image
                  );
                  this.$toast.success("登录成功");
                  this.$router.push({
                    path: "/user",
                  });
                } else {
                  this.modifyTelephone(this.telephoneNumber);
                  this.$router.push({
                    path: "/register",
                  });
                }
              } else {
                this.modifyTelephone(this.telephoneNumber);
                this.$router.push({
                  path: "/register",
                });
              }

              // console.log("验证成功");
            } else {
              if (this.string != "") {
                this.$toast.fail("验证码有误");
              } else {
                this.$toast.fail("请重新获取验证码");
              }
            }
          }
        } else {
          this.$toast.fail("验证码输入框不能为空");
          return;
        }
      } else {
        this.$toast.fail("手机号不能为空");
        return;
      }
    },
    getRandomCode() {
      this.string = "";
      for (let i = 0; i < 6; i++) {
        this.string += code[Math.floor(Math.random() * code.length)];
      }
    },
    copyCodeEvent() {
      this.$refs.verificationText.value = this.string;
      this.isAcceptCode = false;
    },
    ...mapMutations(["modifyTelephone", "modifyUserState", "modifyUserImage"]),
  },
};
</script>