<template>
  <div class="my-bookshelf">
    <div class="bookshelf-header">
      <div class="header-banner">
        <div
          class="header-banner-item"
          v-for="item in bannerData"
          :key="item.type"
          :class="{ 'banner-active': item.type == isActive }"
          @click="changeTextState(item.type)"
        >
          {{ item.text }}
        </div>
      </div>
    </div>
    <div class="bookshelf-body">
      <div class="body-main" v-if="!isLogin || isActive == 'cache'">
        <div class="main-container" v-if="isActive == 'chase'">
          <div class="main-content">
            <div
              :class="{ 'main-content-item': isChaseEmpty }"
              v-for="t in chaseData"
              :key="t.id"
              @click="jumpToDetail(t.id)"
            >
              <div
                class="content-item-img"
                :style="{ backgroundImage: `url(${t.horizontal_cover})` }"
              ></div>
              <div class="content-item-title">{{ t.title }}</div>
              <div class="content-item-other">
                {{ t.is_finish == 1 ? "完结" : `更新至${t.last_ord}话` }}
              </div>
            </div>

            <div class="main-content-add" @click="addDataEvent">+</div>
          </div>
          <div class="main-tips" v-if="isShowTips">
            <span v-show="isChaseEmpty">没有更多啦~~</span
            ><span v-show="!isChaseEmpty">-书架到处空空-</span>
          </div>
          <div
            class="main-recommend"
            v-if="isShowRecommend"
            :class="{ 'bg-color': !isShowLoading }"
          >
            <div class="recommend-header">
              <div class="header-arrow" @click="closeListEvent">
                <span class="iconfont icon-close"></span>
              </div>
              <div class="header-title">你可能喜欢</div>
            </div>
            <div class="recommend-body">
              <div class="body-container" v-if="!isShowLoading">
                <div
                  class="body-container-item"
                  v-for="it in recommendData"
                  :key="it.item_id"
                  @click="DeliverDetailData(it)"
                >
                  <div
                    class="container-item-img"
                    v-lazy:background-image="it.image"
                  ></div>
                  <div class="container-item-title">{{ it.title }}</div>
                  <div class="container-item-des">
                    {{
                      it.comic_info.score > 1
                        ? it.comic_info.score.toFixed(1) + "分"
                        : "新作"
                    }}
                  </div>
                </div>
              </div>
              <div class="loading-icon" v-if="isShowLoading">
                <van-loading type="spinner" color="#1989fa" />
              </div>
            </div>
          </div>
        </div>
        <BookShelfHistoryView v-if="isActive == 'history'" />
        <BookShelfCacheView v-if="isActive == 'cache'" />
      </div>

      <div class="body-empty" v-if="isLogin && isActive != 'cache'">
        <div class="empty-container">
          <div class="empty-container-text">
            登录才能知道自己追了那些漫画哦~~
          </div>
          <div class="empty-container-button" @click="jumpToLoginPage">
            账号登录
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang='scss' scoped>
.my-bookshelf {
  width: 100%;
  height: calc(100% - 94px);
  color: #fff;

  .bookshelf-header {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    &::after {
      position: absolute;
      display: block;
      content: "";
      width: 100%;
      height: 1px;
      left: 0px;
      bottom: 0px;
      background: linear-gradient(90deg, transparent, gray, transparent);
    }

    .header-banner {
      display: flex;
      align-items: center;

      .header-banner-item {
        font-size: 14px;
        padding: 6px 8px;
        margin: 0px 10px;
      }
      .banner-active {
        color: black;
        background: #fff;
        border-radius: 999px;
        box-shadow: 0px 0px 10px gray;
      }
    }
  }

  .bookshelf-body {
    width: 100%;
    height: calc(100% - 50px);
    overflow: scroll;
    &::-webkit-scrollbar {
      width: 0px;
      height: 0px;
    }

    .body-main {
      width: 100%;
      .main-content {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        .main-content-item {
          flex-shrink: 0;
          width: 30%;
          height: 200px;
          margin: 16px 0px 20px 2.5%;
          .content-item-img {
            width: 100%;
            height: 140px;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
          }
          .content-item-title {
            width: 100px;
            margin: 16px 0px 5px 0px;
            font-size: 14px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .content-item-other {
            width: 100px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-size: 13px;
          }
        }
        .main-content-add {
          flex-shrink: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 30%;
          height: 140px;
          margin: 16px 0px 80px 2.5%;
          border: 1px solid #fff;
          font-size: 26px;
        }
      }
      .main-tips {
        text-align: center;
        font-size: 13px;
        color: rgb(214, 213, 213);
      }
      .main-recommend {
        margin-top: 40px;

        .recommend-header {
          display: flex;
          align-items: center;
          height: 40px;
          .header-arrow {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;

            .icon-close {
              font-size: 23px;
            }
          }
          .header-title {
            flex: 1;
            text-align: center;
            margin-right: 20px;
            font-size: 15px;
          }
        }
        .body-container {
          display: flex;
          flex-wrap: wrap;
          width: 100%;
          .body-container-item {
            width: 30%;
            height: 190px;
            margin: 2.5% 0px 0px 2.5%;
            .container-item-img {
              width: 100%;
              height: 140px;
              background-size: cover;
              background-position: center center;
              background-repeat: no-repeat;
            }
            .container-item-title {
              width: 100px;
              height: 25px;
              line-height: 25px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              margin-top: 5px;
              font-size: 14px;
            }
            .container-item-des {
              width: 100px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              font-size: 13px;
              color: rgb(210, 207, 207);
            }
          }
        }
      }
      .bg-color {
        background: rgb(23, 23, 23);
      }
    }

    .body-empty {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      .empty-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .empty-container-button {
          margin-top: 30px;
          padding: 10px 36px;
          border: 1px solid skyblue;
          border-radius: 999px;
        }
      }
    }
  }
}
.loading-icon {
  display: flex;
  justify-content: center;
  padding-top: 20px;
}
</style>
<script>
import { RecommendFeed } from "../../api/index";
import BookShelfHistoryView from "./BookShelfHistoryView.vue";
import BookShelfCacheView from "./BookShelfCacheView.vue";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      bannerData: [
        {
          type: "chase",
          text: "追漫",
        },
        {
          type: "history",
          text: "历史",
        },
        {
          type: "cache",
          text: "缓存",
        },
      ],
      isActive: "chase",
      chaseData: [],
      recommendData: [],
      isChaseEmpty: false,
      isShowRecommend: true,
      isShowTips: true,
      isShowLoading: false,
      isLogin: true,
    };
  },
  created() {
    this.initUserState();
    this.getChaseData();
    this.getRecommendData();
  },
  methods: {
    initUserState() {
      this.isActive = this.changActiveText;
      if (localStorage.user) {
        let currentStr = JSON.parse(localStorage.user);
        if (currentStr[this.telephoneString]) {
          if (currentStr[this.telephoneString].isRun) {
            this.isLogin = false;
          } else {
            this.isLogin = true;
          }
        } else {
          this.isLogin = true;
        }
      } else {
        this.isLogin = true;
      }
    },
    jumpToLoginPage() {
      this.$router.push({
        path: "/login",
      });
    },
    changeTextState(type) {
      if (this.isActive == type) {
        return;
      }
      this.isActive = type;
      this.modifyActiveText(type);
    },
    addDataEvent() {
      this.$emit("jumpPageEvent");
    },
    getChaseData() {
      if (localStorage.chaseDataRecord) {
        let currSt = JSON.parse(localStorage.chaseDataRecord);
        if (currSt[this.telephoneString]) {
          if (currSt[this.telephoneString].length != 0) {
            this.chaseData = currSt[this.telephoneString];
            this.isChaseEmpty = true;
            this.isShowRecommend = false;
          } else {
            this.isChaseEmpty = false;
            this.isShowRecommend = true;
          }
        } else {
          this.isChaseEmpty = false;
          this.isShowRecommend = true;
        }
      } else {
        this.isChaseEmpty = false;
        this.isShowRecommend = true;
      }
    },
    closeListEvent() {
      this.isShowRecommend = !this.isShowRecommend;
      this.isShowTips = false;
    },
    async getRecommendData() {
      this.isShowLoading = true;
      this.recommendData = await RecommendFeed();
      this.isShowLoading = false;
      // console.log(this.recommendData);
    },
    DeliverDetailData(data) {
      let item = {
        ...data,
        season_id: data.item_id,
        horizontal_cover: data.image,
        bottom_info: data.comic_info.main_style_name,
        Did: +new Date(),
      };
      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }

            this.modifyActive(0);
            this.$router.push({
              path: "/detail/" + item.season_id,
            });
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    dealTimeData() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;

      return {
        year,
        month,
        day,
        hour,
        minute,
        second,
      };
    },
    jumpToDetail(id) {
      this.$router.push({
        path: "/detail/" + id,
      });
    },
    ...mapMutations(["modifyActive", "modifyActiveText"]),
  },
  computed: {
    ...mapState(["telephoneString", "changActiveText"]),
  },
  components: {
    BookShelfHistoryView,
    BookShelfCacheView,
  },
};
</script>