import {Tab, Tabs,Swipe, SwipeItem,Loading,Lazyload,Toast,Image as VanImage } from 'vant'

const vantComponents = [Tab, Tabs,Swipe, SwipeItem,Loading,Lazyload,Toast,VanImage ]

export default {
    install(Vue) {
        vantComponents.forEach(Component => {
            Vue.use(Component)
        })
    }
}