<template>
  <div class="user-information">
    <div class="user-header">
      <div class="header-left" @click="jumpPageEvent">
        <span class="iconfont icon-arrowleft"></span>
      </div>
      <div class="header-right" @click="jumpToSetting">
        <span class="iconfont icon-setting"></span>
      </div>
    </div>
    <div class="user-body">
      <div class="body-user-message">
        <div
          class="user-message-logo"
          :style="{ backgroundImage: `url(${imageUrl})` }"
        >
          <span class="iconfont icon-user" v-if="!loginState"></span>
        </div>
        <div class="user-message-name">
          <div class="message-name-text" v-if="loginState">{{ userName }}</div>
          <div
            class="message-name-button"
            v-if="!loginState"
            @click="JumpToLogin"
          >
            账号登录
          </div>
        </div>
      </div>
      <div class="body-other" v-if="!isShowOther">
        <div class="body-other-first">
          <div
            class="other-first-item"
            v-for="item in firstPartData"
            :key="item.weight"
          >
            <div
              class="first-item-icon"
              v-lazy:background-image="item.icon"
            ></div>
            <div class="first-item-text">{{ item.title }}</div>
          </div>
        </div>
        <div class="body-other-second">
          <div
            class="other-second-item"
            v-for="item in secondPartData"
            :key="item.weight"
          >
            <div
              class="second-item-icon"
              v-lazy:background-image="item.icon"
            ></div>
            <div class="second-item-text">{{ item.title }}</div>
          </div>
        </div>
      </div>
      <div class="other-loading" v-if="isShowOther">
        <van-loading type="spinner" color="#1989fa" />
      </div>
    </div>
    <router-view />
  </div>
</template>
<style lang='scss' scoped>
.user-information {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  color: #fff;
  background: black;

  .user-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;

    .header-left,
    .header-right {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;

      .icon-arrowleft,
      .icon-setting {
        font-size: 20px;
      }
    }
  }

  .user-body {
    width: 100%;

    .body-user-message {
      position: relative;
      display: flex;
      align-items: center;
      padding: 0px 20px;
      height: 120px;
      &::after {
        position: absolute;
        display: block;
        content: "";
        width: 100%;
        height: 1px;
        bottom: 0px;
        left: 0px;
        background: linear-gradient(
          90deg,
          transparent,
          rgb(22, 22, 22),
          transparent
        );
      }

      .user-message-logo {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        margin: 0px 10px;
        border: 1px solid #777;
        border-radius: 999px;
        box-shadow: 0px 0px 5px #fff;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;

        .icon-user {
          font-size: 20px;
          color: gray;
        }
      }

      .user-message-name {
        margin-left: 30px;

        .message-name-text {
          font-size: 20px;
        }

        .message-name-button {
          padding: 8px 20px;
          font-size: 14px;
          border: 1px solid #777;
          border-radius: 999px;
          box-shadow: 0px 0px 3px #fff;
        }
      }
    }
    .body-other {
      margin: 30px 20px 0px;
      .body-other-first {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-bottom: 60px;
        .other-first-item {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          .first-item-icon {
            width: 50px;
            height: 50px;
            border-radius: 999px;
            margin-bottom: 20px;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            box-shadow: 0px 0px 6px #fff;
          }
          .first-item-text {
            font-size: 14px;
          }
        }
      }
      .body-other-second {
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        .other-second-item {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 25%;
          margin-bottom: 20px;

          .second-item-icon {
            width: 30px;
            height: 30px;
            border-radius: 999px;
            box-shadow: 0px 0px 6px gray;
            margin-bottom: 20px;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
          }
          .second-item-text {
            font-size: 13px;
          }
        }
      }
    }
    .other-loading {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 300px;
    }
  }
}
</style>
<script>
import { UserMessage } from "../../api/index";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      firstPartData: [],
      secondPartData: [],
      isShowOther: false,
      loginState: false,
      isCanRun: true,
      userName: "",
      imageUrl: "",
    };
  },
  created() {
    this.getUserMessage();
    this.renderUserMessage();
  },
  methods: {
    async getUserMessage() {
      this.isShowOther = true;
      let currentData = await UserMessage();
      this.firstPartData = currentData.filter((item) => {
        return item.weight > 12;
      });
      this.secondPartData = currentData.filter((item) => {
        return item.weight < 13;
      });
      this.isShowOther = false;
    },
    jumpPageEvent() {
      this.$router.push({
        path: "/",
      });
    },
    JumpToLogin() {
      this.$router.push({
        path: "/login",
      });
    },
    jumpToSetting() {
      this.$router.push({
        path: "/user/setting",
      });
    },
    renderUserMessage() {
      if (localStorage.user) {
        let currentStorage = JSON.parse(localStorage.user);
        for (let key in currentStorage) {
          if (currentStorage[key].isRun) {
            this.imageUrl = currentStorage[key].image;
            this.userName = currentStorage[key].name;
            this.modifyTelephone(key);
            this.loginState = true;
            this.isCanRun = false;
            return;
          }
        }

        if (this.isCanRun) {
          this.loginState = false;
          this.userName = "";
          this.imageUrl = "";
        }
      } else {
        this.loginState = false;
        this.userName = "";
        this.imageUrl = "";
      }
    },
    ...mapMutations(["modifyTelephone"]),
  },
  computed: {
    ...mapState(["telephoneString"]),
  },
  watch: {
    // telephoneString(){
    //   this.renderUserMessage();
    //   console.log('执行中...')
    // }
  },
};
</script>